/* Général */
body {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-size: 1.2em;
}

h2 {
    margin: 0;
}

li {
    list-style: none;
}

i {
    cursor: pointer;
}


/* Général - main */
main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

/* Général - section */
section {
    width: 90%;
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

/* Général - article */
article {
    background-color: white;
}

/* Général - icônes */
i {
    font-size: 1em;
}

/* Général - Picture-img */
.picture {
    border-radius: 50%;

}

img {
    object-fit: cover;
}

/* Général - Formulaire */
form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

input {
    border-radius: 10px;
    border: 1px solid rgb(180, 180, 180);
    height: 50px;
    width: 300px;
    margin-bottom: 10px;
}

.error {
    color: red;
}

::placeholder {
    padding-left: 5px;
    font-size: 1.4em
}

textarea {
    resize: none;
    font-family: Arial, Helvetica, sans-serif;
    width: 95%;
    height: 50px;
    border-radius: 10px;
    outline: none;
}

/* Général - Button */
.button {
    height: 60px;
    width: 300px;
    border-radius: 50px;
    border: none;
    color: white;
    font-weight: 800;
    margin: 20px 0;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.green-button {
    background-color: green;
}

.red-button {
    background-color: rgba(255, 0, 0, 0.726);
}

/* Components / Navbar */
header {
    /* background-color: rgba(255, 0, 0, 0.726); */
    background-color: rgb(63, 58, 58);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
}

.header-container-img {
    margin-left: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-img-groupomania {
    width: 180px;
    height: 100px;
    object-fit: cover;
}

header nav {
    margin-right: 10px;
    width: 180px;
    display: flex;
    justify-content: flex-end;
}

header nav ul {
    display: flex;
    justify-content: space-between;
    width: 120px;
}

header nav ul li i {
    color: white;
}

/* Components / Footer */
footer {
    background-color: rgba(255, 0, 0, 0.726);
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 1.2em;
    font-weight: 800;
}


/* Pages/ Home */
#log-presentation {
    position: relative;
    color: white;
    height: 300px;
    width: 100%;
    font-size: 1.2em;
}

#main-log div img {
    position: absolute;
    width: 100%;
    height: 300px;
    object-fit: cover;
}

.log-box {
    position: relative;
    width: 100%;
    height: 300px;
    background-color: black;
    opacity: 0.4;
}

#main-log h1 {
    border: 3px solid white;
    background-color: transparent;
    width: 90%;
    height: 300px;
    position: absolute;
    top: 60%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: none;
    opacity: 2;
}

/* Components/ Log / index */
#form-container {
    display: flex;
    flex-direction: column-reverse;
}

.form-home-link {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 10px 0 50px 0;
}

.form-home-link p:hover {
    color: rgb(66, 66, 253);
}

.form-home-link p {
    color: black;
    margin: 0;
    border-radius: 0;
    height: 40px;
    cursor: pointer;
}

/* Components/ Log / SignIn  */
#login {
    margin: 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.login-borderBottom {
    border-top: 1px solid black;
    width: 250px;
    margin-bottom: 20px;
}

/* Components / Profil / ProfilUser */
#article-account-name-img {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}

#article-account-name-img h1 {
    margin: 0 0 10px 0;
}

.container-account-description {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

}

.account-description {
    border-radius: 20px;
    height: 150px;

}

.account-description p {
    width: 300px;
    height: 150px;
    border: 2px solid rgb(192, 192, 192, 0.3);
    border-radius: 10px;
    margin: 0;
    padding: 5px;
    font-size: 1.1em;
}


#description {
    width: 300px;
    height: 150px;
    font-size: 1.1em;
    padding: 5px;
}

.buttonSend-description {
    width: 180px;
    margin-bottom: 0;
}

/* Components / Profil / UpdateImg */
.form-updatePicture {
    height: 290px;
}

.container-userProfile-picture {
    margin-top: 20px;
    height: 230px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.container-account-picture {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 0.5px solid black;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}

.container-account-picture img {
    width: 200px;
    height: 200px;
}

.buttonChoice-picture {
    position: relative;
    display: flex;
    justify-content: center;
    bottom: 80px;
}

.buttonChoice-picture i {
    background-color: rgb(229, 226, 226);
    color: black;
    font-size: 0.7em;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.input-file {
    border: 0.5px solid black;
    width: 40px;
    position: relative;
    bottom: 65px;
    left: 80px;
    height: 40px;
    border-radius: 50%;
    opacity: 0;
}

.sendUpdatePicture-user {
    position: relative;
    bottom: 70px;
    border: none;
    background-color: rgb(229, 226, 226);
    font-size: 0.7em;
    font-weight: bold;
    width: 70px;
    height: 30px;
    border-radius: 20px;
}

/* Components / Post / allPosts */

.section-title-post {
    width: 100%;
    display: flex;
    align-items: center;
    font-size: 0.8em;
    margin-bottom: 10px;
}

.img-title-post {
    width: 40px;
    margin-right: 10px;
}


/* Components / Post / CardAllPosts */

.article-post {
    width: 100%;
    margin: 0 0 40px 0;
    padding: 10px 10px 0 10px;
    border-radius: 20px;
    box-shadow: 10px 10px 10px 10px rgb(0 0 0 / 15%);
}

.article-post i {
    font-size: 0.8em;
}

.fa-spinner {
    color: rgba(255, 0, 0, 0.726);
    font-size: 1.5em;
}

.header-post {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 1.2em;
}

.header-post-user {
    display: flex;
    width: 50%;
    justify-content: space-around;
}

.pictureUser-post {
    width: 80px;
    height: 80px;
}

.container-editDelete-post {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.9em;
    width: 17%;
    margin-bottom: 40px;
}

.fa-edit {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
}

.container-datePost {
    display: flex;
    justify-content: space-between;
    font-style: italic;
}

.img-post {
    width: 100%;
    border-radius: 5px;
}

.video {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;

}

.video iframe {
    border-radius: 10px;
}

.block-comments {
    margin: 5px 0 20px 0;
    display: flex;
    justify-content: flex-end;
}

/* Components / Post / DeletePost */
.fa-trash-alt:hover {
    color: rgba(255, 0, 0, 0.726);


}

/* Components / Post / NewPostForm */
.section-newPost {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-bottom: 10px;
}

.article-newPost {
    width: 95%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0;
    border-bottom: 1px solid rgb(63, 58, 58, 0.3);
}

.article-newPost input {
    border-radius: 50px;
    border: none;
    background-color: rgb(250, 246, 246);
}

.picture-newPost {
    width: 70px;
    height: 70px;
    border: 0.5px solid grey;
}


/* Components / Post / CarAllPost & newPostForm - article popup */
.article-popup {
    position: absolute;
    overflow-y: scroll;

}


.style-popup {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.7);
    transition: opacity .4s;
}

.container-popup {
    background: white;
    border-radius: 20px;
    width: 90%;
    margin: 100px auto;
    padding: 20px 0;
}

.close-popup {
    display: flex;
    justify-content: flex-end;
    width: 95%;
    color: red;
    margin-bottom: 10px;

}

.header-popup {
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-popup-newPost {
    justify-content: flex-start;
    margin-bottom: 20px;
}

.textPopup {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.textPopup textarea {
    height: 100px;
}

.img-newPost-popup {
    margin-top: 10px;
    width: 90%;
    border-radius: 10px;
    object-fit: cover;
}

#containerVideo-Newpost {
    width: 95%;
    display: flex;
    flex-direction: row-reverse;
    margin: 10px 0;
}

#closeVideo-nexPost {
    position: absolute;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    background-color: rgb(226, 223, 223, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.6em;
    margin: 5px;
}

.video-newPost {
    width: 100%;
    height: 300px;
    margin: auto;
}

.container-newPostPopup-img-btn {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 95%;
}

.newPostPopup-img {
    width: 98%;
    height: 30px;
    display: flex;
}

.fa-image {
    font-size: 1.2em;
}

#file-upload {
    opacity: 0;
    width: 25px;
    border: none;
    position: relative;
    right: 25px;
}

.container-newPostBtnSendCancel {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.btnNewPost {
    width: 115px;
    height: 35px;
    font-weight: 600;
}

/* Components / Post / CardComment  */
.container-comments {
    overflow-y: scroll;
    max-height: 500px
        /* height: 500px; */
}

.containerComment-User {
    display: flex;
    flex-direction: column;
    padding: 7px;
    border: 0.1px solid rgb(143, 143, 143);
    border-radius: 20px;
    background-color: rgb(158, 157, 157);
    color: white;
}

.containerComment-client {
    display: flex;
    flex-direction: column;
    padding: 7px;
    border: 0.1px solid rgb(39, 39, 39);
    border-radius: 20px;
    margin-bottom: 10px;
}

.profileUser-comment {
    display: flex;
    justify-content: space-between;
}

.userImgName-comment {
    display: flex;
}

.pictureUser-comment {
    width: 40px;
    height: 40px;
    border-radius: 10px;
}

.userImgName-comment h4 {
    margin: 0 5px;
    font-weight: 600;
}

.date-comment {
    margin-right: 5px;
    font-style: italic;
    color: rgb(143, 143, 143);
}

.text-comment {
    word-wrap: break-word;
    margin: 5px 0;
    font-size: 1em;
    font-weight: 500;
}

#textarea-comment {
    margin-top: 5px;
    height: 80px;
}

#textarea-comment::placeholder {
    padding-top: 10px;
}


.SendComment-button {
    position: relative;
    left: 120px;
    background-color: rgb(46, 45, 45);
    width: 30%;
    height: 50px;
    margin: 0 0 10px 0;
}

/* Components / Post / EditDeleteComment  */

.close-editcomment {
    display: flex;
    justify-content: flex-end;
    width: 95%;
    color: red;
    margin-bottom: 10px;

}

.editDelete-comment {
    display: flex;
    font-size: 0.7em;
    justify-content: space-around;
    align-items: center;
    height: 20px;
    margin-bottom: 5px;
    width: 250px;
}


@media screen and (min-width:768px) {

    /* Général / icônes */
    i {
        font-size: 1.3em;
    }

    /* Components / Navbar */
    .header-container-img {
        width: 70%;
        margin-left: 20px;
    }

    .header-img-groupomania {
        width: 300px;
    }

    header nav {
        width: 500px;
    }

    header nav ul {
        width: 300px;
    }

    /* Pages / Home */
    #main-log {
        display: flex;
        justify-content: space-around;
    }

    #main-log div img {
        height: 600px;
    }

    .log-box {
        height: 600px;
    }

    #main-log div h1 {
        border: 3px solid white;
        background-color: transparent;
        width: 90%;
        height: 300px;
        position: absolute;
        top: 60%;
        left: 50%;
        transform: translate(-50%, -50%);
        border: none;
        opacity: 2;
    }

    /* Components / Log / index */
    #form-container {
        width: 35%;
        flex-direction: column-reverse;
        justify-content: none;
        align-items: center;
        border-radius: 20px;
        box-shadow: 0px 10px 10px 5px rgb(0 0 0 / 25%);
        margin-top: 20px;
        height: 600px;
    }

    .form-home-button {
        height: 100px;
        margin: 0 0 40px 0;
        width: 100%;
    }

    .nav-button {
        border-bottom: 0.5px solid black;
        width: 47%;
    }

    /* Components / Log / SignIn */
    .login-borderBottom {
        margin-bottom: 40px;
    }

    /* Components / Log / SignUp */
    #registration {
        width: 30%;
        margin: auto;
    }

    #registration h4 {
        color: green;
        font-size: 1.3em;
    }


    /* Components / Profil / ProfilUser */
    #section-account-name-img {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 20px;
    }

    /* Components / Post / allPosts */

    #section-post {
        width: 100%;
    }

    .section-title-post {
        margin: 0 400px 40px 0;
    }

    /* Components / Post / CardAllPosts */
    .article-post {
        width: 75%;
    }

    .pictureUser-post {
        width: 100px;
        height: 100px;
    }

    /* Components / Post / NewPostForm */
    .article-newPost {
        width: 60%;
        height: 100px;
        justify-content: center;
        margin-bottom: 20px;
    }

    .picture-newPost {
        width: 70px;
        height: 70px;
    }
}